/**
 * @event selector-click - 点击选择器触发的事件
 */
/* eslint-disable */

/**
 * @noteType component
 * @name MintPickerSelect
 * @tagName mt-picker-select
 * @desc 文本域(带选项)
 * @wrapClassName mint-picker-select
 * @html
 * <mt-text-select label="标题文字"></mt-text-select>
 */
export default {
  name: 'mt-picker-select',
  props: {
    value: { default: '' },
    // url: { type: String, default: '' },
    /**
       * @noteType prop
       * @field rows
       * @desc 行数
       * @type input
       * @value 4
       */
    rows: String,
    /**
       * @noteType prop
       * @field rows
       * @desc 行数
       * @type input
       * @value 100
       */
    maxlength: String,
    /**
     * @noteType prop
     * @field label
     * @desc 标题
     * @type input
     * @value 标题
     */
    label: { type: String, required: true },
    /**
     * @noteType prop
     * @field placeholder
     * @desc 占位文本
     * @type input
     * @value 请选择
     */
    placeholder: { type: String, default: '请选择' },
    /**
     * @noteType prop
     * @field options
     * @desc 选项字典数据
     * @type textarea
     * @value
     */
    options: { type: Array, required: true },
    disabled: Boolean,
    readonly: Boolean,
    attr: Object
  },
  data() {
    return {
      selectorShow: false,
      currentValue: "",
      popupVisible: false
    };
  },
  watch: {
    //
  },
  methods: {
    singleSelectDisplay () {
      if (this.value === '') return this.placeholder
      if (this.options.length === 0) {
        this.$emit('selector-click', e)
        return ''
      }
      return this.currentValue;
    },
    onChange() {
      //
    }
  }
};




